<template>
  <div id="app">
    <Clock/>
    <Alert/>
  </div>
</template>

<script>
  import Clock from './components/Clock.vue'
  import Alert from './components/Alert'

  export default {
    name: 'app',
    components: {
      Clock,
      Alert
    },
    data () {
      return {
        holiday: {},
        error: null
      }
    },
    mounted () {
      this.$http.get('127.0.0.1:3000/holidays').then((res) => {
        console.log(res)
        res.error
          ? this.error = res.error
          : this.holiday = res
      }).catch((e) => {
        console.log(e.stack)
      })
    }
  }
</script>

